/* ---- estilos para el react-datepicker ---- */

.buscador-datepicker {
  width: 100%;
}
.react-datepicker {
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  width: 100%;
}
.react-datepicker__input-container input {
  outline: none;
}
.react-datepicker__header {
  background-color: white;
  border-bottom: none;
}

.react-datepicker__navigation {
  color: var(--colorDos);
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
}
.react-datepicker__navigation:hover {
  background-color: #1aa198;
  transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -ms-transition: 0.2s ease all;
  -o-transition: 0.2s ease all;
}

.react-datepicker__navigation-icon::before {
  border-color: var(--colorDos);
  color: var(--colorDos);
}
.react-datepicker__navigation:hover *::before {
  border-color: var(--colorDos);
}

.react-datepicker__month-container {
  position: relative;
}
.react-datepicker__month-container:last-child:after {
  content: "";
  position: absolute;
  background: var(--colorFondoOscuro);
  left: 0;
  bottom: 5%;
  height: 90%;
  width: 3px;
}

.react-datepicker__day-name {
  color: var(--colorTres);
  font-weight: bold;
}
/* día */
.react-datepicker__day:hover {
  background-color: rgba(0, 0, 0, 0.25);
  border-radius: 0.4rem;
  color: white;
}
.react-datepicker__day--selected,
.react-datepicker__day--in-selecting-range,
.react-datepicker__day--in-range {
  border-radius: 1rem;
  background-color: var(--colorUno) !important;
  color: white !important;
}
.react-datepicker__day--keyboard-selected {
  color: inherit;
  background-color: inherit;
}
.react-datepicker__day--keyboard-selected.react-datepicker__day--disabled {
  color: #ccc;
  background-color: #fff;
}

.react-datepicker__day--excluded {
  color: #ccc;
}

/* -- hoy -- */
.react-datepicker__day--today:not(.react-datepicker__day--outside-month) {
  border: 0.1rem solid var(--colorTres);
  border-radius: 1rem;
  line-height: 1.5rem; /* compensa el ancho del borde */
  font-weight: normal;
  box-sizing: border-box;
}
.react-datepicker__day--today:not(.react-datepicker__day--in-range):not(.react-datepicker__day--in-selecting-range):not(.react-datepicker__day--excluded):not(.react-datepicker__day--disabled) {
  background-color: white !important;
  color: #000 !important;
}

.card-categoria{
    border: 3px solid transparent;
    box-sizing: border-box;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    margin: 1rem;
    cursor: pointer;
}

.card-categoria:hover {
    filter:saturate(130%);
    overflow:hidden;
    -webkit-filter:saturate(130%);
    transform: scale(1.01);
    -webkit-transform: scale(1.01);
    -moz-transform: scale(1.01);
    -ms-transform: scale(1.01);
    -o-transform: scale(1.01);
    transition: .2s ease all;
    -webkit-transition: .2s ease all;
    -moz-transition: .2s ease all;
    -ms-transition: .2s ease all;
    -o-transition: .2s ease all;
}

.card-categoria-img {
    height: 230px;
    transition: .4s ease-in all;
    -webkit-transition: .4s ease-in all;
    -moz-transition: .4s ease-in all;
    -ms-transition: .4s ease-in all;
    -o-transition: .4s ease-in all;
}

.card-categoria-img img{
    width: 100%;
    height: 100%;
    border-radius: 10px 10px 0px 0px;
    object-fit: cover;
    -webkit-border-radius: 10px 10px 0px 0px;
    -moz-border-radius: 10px 10px 0px 0px;
    -ms-border-radius: 10px 10px 0px 0px;
    -o-border-radius: 10px 10px 0px 0px;
}

.card-categoria-info{
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 23px;
    color: var(--colorDos);
    padding: 1rem;
}

.card-categoria-info p{
    font-size: 14px;
    line-height: 16px;
    opacity: 0.6;
    padding: 0.5rem 0;
}

.card-categoria .con-filtro {
    height: 0px;
}

.card-categoria.filtro-activo {
    border: 3px solid var(--colorTres);
}

.categoria-list{
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    color: var(--colorDos);
    padding: 1rem;
}

.categoria-list li{
    display: flex;
}

.categoria-list .ver-todas {
    display: none;
}

.categoria-list .ver-todas-con-filtro {
    display: flex;
    font-size: 0.7em;
    color: var(--colorUno);
    cursor: pointer;
    transition: .4s ease all;
}

@media (min-width: 480px) {
    .categoria-list ul {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
    
    .categoria-list .ver-todas-con-filtro {
