  width: 148px;
}

/* -- index -- */
.productos-imagenes-galeria .image-gallery-index {
  top: 364px; /* height + 4 */
  right: calc(50% - 50px);
  color: var(--colorDos);
  font-weight: bold;
  background: none;
}

/* -- flechitas nav customizadas -- */
.productos-imagenes-galeria .image-gallery-custom-right-nav,
.productos-imagenes-galeria .image-gallery-custom-left-nav {
  position: absolute;
  top: 40%;
  z-index: 4;
  margin: 10px;
