:root {
  --colorUno: #1dbeb4;
  --colorDos: #383b58;
  --colorTres: #545776;
  --colorCuatro: #f3f1ed;

  --colorLink: #4285f4;
  --colorError: #ff392e;
  --colorFondoOscuro: rgba(84, 87, 118, 0.1);
  --colorFav: #e31b23;

  --fs-xs: 12px;
  --fs-sm: 14px;
  --fs-md: 16px;
  --fs-lg: 20px;
  --fs-xl: 24px;

  --font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", Oxygen,
  Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif; 
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  list-style: none;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", Oxygen,
  Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-family: var(--font-family);
}


body {
  min-height: calc(100vh - 80px);
}

/* --------------- Estilos de los botones y links ----------- */

.input {
  outline: none;
  border: none;
  box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.15);
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}
.input:focus,
.input:active {
  box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.3);
}

.input-checkbox {
  height: 18px;
  width: 18px;
}

.label-required::after {
  content: " * ";
  color: #e31b23;
  color: var(--colorFav);
}

/* --------------- Estilos de los botones y links ----------- */

a {
  color: #4285f4;
  color: var(--colorLink);
  text-decoration: none;
}

.link:hover {
  -webkit-text-decoration: underline 2px solid;
          text-decoration: underline 2px solid;
}

.btn {
  height: 40px;
  margin: 0 0.5em;
  background: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
  text-align: center;
  line-height: 38px;
  font-size: 16px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  box-shadow: 0 2px 4px rgba(165, 156, 156, 0.12);
}

.btn-primario {
  background-color: #1dbeb4;
  background-color: var(--colorUno);
  color: #f3f1ed;
  color: var(--colorCuatro);
  transition: 0.1 ease all;
  -webkit-transition: 0.1s ease all;
  -moz-transition: 0.1s ease all;
  -ms-transition: 0.1s ease all;
  -o-transition: 0.1s ease all;
  border: 1px solid #1dbeb4;
  border: 1px solid var(--colorUno);
}
.btn-primario:hover {
  background-color: #1aa198;
  border: 1px solid #1aa198;
}
.btn-primario:active,
.btn-primario:focus,
.btn-primario:focus-visible {
  border: 1px solid #383b58;
  border: 1px solid var(--colorDos);
}

.btn-secundario {
  color: #1dbeb4;
  color: var(--colorUno);
  border: 1px solid #1dbeb4;
  border: 1px solid var(--colorUno);
  transition: 0.1s ease all;
  -webkit-transition: 0.1s ease all;
  -moz-transition: 0.1s ease all;
  -ms-transition: 0.1s ease all;
  -o-transition: 0.1s ease all;
}
.btn-secundario:hover {
  color: #545776;
  color: var(--colorTres);
  border-color: #545776;
  border-color: var(--colorTres);
  box-shadow: 2px 2px 5px 1px rgba(0, 0, 0, 0.08);
}
.btn-secundario:active {
  background-color: rgba(84, 87, 118, 0.1);
  background-color: var(--colorFondoOscuro);
}

.btn-xs {
  width: 164px;
}
.btn-sm {
  width: 206px;
}
.btn-md {
  width: 296px;
}
.btn-lg {
  width: 346px;
}
.btn-xl {
  width: 362px;
}

